/* Import Montserrat font */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');

/* Base Styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #f5f5f5;
  color: #333333;
}

/* Container */
.container {
  display: flex;
  min-height: 100vh;
}

/* Sidebar */
.sidebar {
  width: 250px;
  background-color: #1a1a1a;
  color: #ffffff;
  padding: 20px;
  box-sizing: border-box;
}

.sidebar h2 {
  margin-top: 0;
  font-weight: 700;
  font-size: 24px;
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 30px;
}

.tab-btn,
.active-tab-btn {
  background-color: transparent;
  border: none;
  padding: 12px 16px;
  border-radius: 5px;
  color: #ffffff;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
  text-align: left;
  transition: background-color 0.3s, color 0.3s;
}

.tab-btn:hover {
  background-color: #333333;
}

.active-tab-btn {
  background-color: #ffffff;
  color: #1a1a1a;
}

.badge {
  background-color: #d4af37;
  color: #ffffff;
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 12px;
  margin-left: 8px;
  font-weight: bold;
}

/* Main Content */
.main {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  background-color: #ffffff;
}

/* Form Styles */
.form-row {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  flex-wrap: wrap;
}

.input,
.textarea {
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #cccccc;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
}

.input {
  flex: 1 1 150px;
}

.textarea {
  min-height: 60px;
  resize: vertical;
}

.primary-btn {
  background-color: #d4af37;
  color: #ffffff;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  font-weight: 700;
  cursor: pointer;
  margin-top: 10px;
  transition: background-color 0.3s;
}

.primary-btn:hover {
  background-color: #bfa133;
}
/* Container for multiple product images in product card */
.product-images {
  display: flex;
  gap: 6px;
  margin-top: 8px;
  justify-content: center;
  flex-wrap: wrap;
}

.product-images img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 4px;
  border: 1px solid #ccc;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.product-images img:hover {
  transform: scale(1.1);
  border-color: #d4af37; /* highlight color */
}

/* File input styling for multiple image uploads */
.input-file {
  border: 1px solid #cccccc;
  padding: 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  color: #555;
}

/* Show previews of uploaded images in form */
.image-previews {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  flex-wrap: wrap;
}

.image-previews img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 4px;
  border: 1px solid #ccc;
}


/* Product Grid */
.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.product-card {
  background-color: #f9f9f9;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
}

.product-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 6px;
}


.product-card h4 {
  margin: 10px 0 5px;
  font-size: 18px;
  font-weight: 600;
}

.product-card p {
  margin: 5px 0;
  font-size: 14px;
  color: #666666;
}

.small-btn {
  background-color: #1a1a1a;
  color: #ffffff;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  margin: 5px 5px 0 0;
  transition: background-color 0.3s;
}

.small-btn:hover {
  background-color: #333333;
}

.small-btn.delete {
  background-color: #d9534f;
}

.small-btn.delete:hover {
  background-color: #c9302c;
}

/* Order List */
.order-list {
  list-style-type: none;
  padding: 0;
  margin-top: 20px;
}

.order-item {
  padding: 15px;
  border: 1px solid #e0e0e0;
  margin-bottom: 10px;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f9f9f9;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: #ffffff;
  padding: 25px;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

/* Responsive Design */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
  }

  .main {
    padding: 10px;
  }

  .form-row {
    flex-direction: column;
  }
}
